  //     var title = "";
  //     var author = "";
  //     var blogtxt = "";
  //     var url = "";
  //    useEffect(() => {
  //       console.log("counter updated");
  //       title = title2;
  //       console.log(title);
  //       author = author2;
  //       blogtxt = props.blogtext;
  //       url = props.url;
  //     }, [title2, author2]);
  //     if (title2 === "" && author2 === "") {
  //       title = props.title;
  //      author = props.author;
  //      blogtxt = props.blogtext;
